import React from 'react';
import {Typography} from "@material-ui/core";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Box from "@material-ui/core/Box";
import Forum from "../forum/Forum";
import {authentication} from "../../services/authentication";
import ProjectMembers from "./ProjectMembers";
import {Create} from "../joindemand";
import {Link} from "react-router-dom";
import CustomMaterialButton from "../../utils/CustomMaterialButton";
import MyProjectDemands from "../dashboard/MyProjectDemands";
import CustomBoostButton from "../../utils/CustomBoostButton";
import Chip from "@material-ui/core/Chip";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={0}>{children}</Box>}
    </Typography>
  );
}
function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const ProjectBody  = props => {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const hasAlreadyLikeTheProjectChecker = (item, user) => {
    let check = false;
    if (!user) {
      return check;
    }
    user.joinDemands.forEach(joindemand => {
      if (joindemand.relatedProject['@id'] === item['@id']) {
        check = true
      }
    });
    return check;
  };

  return (
    <>
        <div className="d-flex justify-content-center">
          <Tabs
            onChange={handleChange}
            variant="scrollable"
            scrollButtons="auto"
            value={value}
            className={'text-center'}
          >
            <Tab label={'Description'}
                 {...a11yProps(0)}
            />
            <Tab label={'Propositions'}
                 {...a11yProps(1)}
            />
            <Tab label={'Membres'}
                 {...a11yProps(2)}
            />
            <Tab label={'Demandes'}
                 {...a11yProps(3)}
            />
          </Tabs>
        </div>

        <TabPanel value={value} index={0}>
          <div className="d-flex justify-content-between align-items-center">
            <Typography variant="h4" gutterBottom className={"max-w-75"}>
              {props.project.pitch}
            </Typography>
            <div className="d-flex align-items-center">
              <CustomBoostButton item={props.project} user={props.user} handleBoost={() => props.projectLiking ? null : props.handleProjectLike(props.project)}/>
              <Typography variant={'h6'}>
                {props.project.likes}
              </Typography>
            </div>
          </div>
          <div className="d-flex flex-column justify-content-between align-items-between">
            <div className="d-flex flex-column justify-content-between">
              <Typography variant={'h5'} gutterBottom>
                {props.project.initiator &&
                <>
                  {props.project.initiator.firstName + (' ' + (props.project.initiator.mainSkill ? props.project.initiator.mainSkill : ''))}
                </>
                }
              </Typography>
              <div className="d-flex flex-wrap">
                {props.project.categories.map((category, index) => (
                  <div className="mr-2 mb-2" key={index}>
                    <Chip
                      label={category}
                      color="primary"
                    />
                  </div>
                ))}
              </div>
            </div>
          </div>

          <Typography variant={'body1'} className={"my-3"}>
            <span dangerouslySetInnerHTML={{__html: props.project.description}}></span>
          </Typography>
          <div className={"text-center"}>
            {hasAlreadyLikeTheProjectChecker(props.project) ? (
              <p>Vous avez demand  rejoindre ce projet !</p>
            ) : (
              <div>
                {authentication.currentUserValue &&
                <div className={""}>
                  <Create {...props} demander={authentication.currentUserValue['@id']} project={props.project['@id']} status={'pending'}/>
                </div>
                }
                {!authentication.currentUserValue &&
                  <div>
                    <Link to="/se-connecter">
                      <CustomMaterialButton width={'auto'} text={'Rejoindre le projet'} color={'primary'}/>
                    </Link>
                  </div>
                }
              </div>
            )}
          </div>
        </TabPanel>
        <TabPanel value={value} index={1}>
          <Forum
            project={props.project}
            handleMessageLike={props.handleMessageLike}
            handleMessageDislike={props.handleMessageDislike}
            messageLiking={props.messageLiking}
            user={props.user}
          />
        </TabPanel>
      <TabPanel value={value} index={2}>
          <ProjectMembers project={props.project}/>
      </TabPanel>
      <TabPanel value={value} index={3}>
        <MyProjectDemands {...props} project={props.project}/>
      </TabPanel>

    </>
  )
}
export default ProjectBody
