import React from 'react'
import { Field, reduxForm } from 'redux-form'
import TextField from "@material-ui/core/TextField";
import {Typography} from "@material-ui/core";
import CustomMaterialButton from "../../utils/CustomMaterialButton";

const validate = values => {
  const errors = {}
  const requiredFields = [
    'pitch',
  ]
  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = 'Ce champ est requis'
    }
  })
  return errors
}

const renderTextField = ({
                           label,
                           input,
                           meta: { touched, invalid, error },
                           ...custom
                         }) => (
  <TextField
    label={label}
    placeholder={label}
    error={touched && invalid}
    helperText={touched && error}
    {...input}
    {...custom}
  />
)

const WizardProjectFormFirstPage = (props) => {
  const { handleSubmit, pristine, submitting } = props

  return (
    <form onSubmit={handleSubmit}>
      <div>
        <Typography variant={"h5"}>Pitch : Rsumez votre projet en une phrase simple</Typography>
      </div>
      <div className={"mt-3"}>
        <Field
          component={renderTextField}
          name="pitch"
          type="text"
          label="Pitch"
          fullWidth
          required={true}
        />
      </div>
      <div className="d-inline-block mt-3">
          <CustomMaterialButton disabled={pristine || submitting} type="submit" color={'primary'} text={'Suivant'}/>
      </div>
    </form>
  )
}

export default reduxForm({
  form: 'project-wizard',              // <------ same form name
  validate,
  destroyOnUnmount: false,     // <------ preserve form data
  enableReinitialize: true,
  keepDirtyOnReinitialize: true
})(WizardProjectFormFirstPage)
