import React from 'react';
import Layout from "../components/block/Layout";
import {Typography} from "@material-ui/core";
import UserAccessor from "../components/dashboard/UserAccessor";
import {authentication} from "../services/authentication";
import CustomMaterialButton from "../utils/CustomMaterialButton";

export default class AccountPage extends React.Component {
  logout() {
    authentication.logout()
    this.props.history.push('/')
  }

  render() {
    return (
      <>
        <Layout {...this.props}>
          <div className="container">
            <div className="row">
              <div className="col">
                <Typography variant="h4" component="h4" gutterBottom className={'text-center'}>
                  {authentication.currentUserValue.firstName} {authentication.currentUserValue.lastName}
                </Typography>
                <Typography variant="h6" className={"text-center"}>{authentication.currentUserValue.mainSkill}</Typography>
              </div>
            </div>
          </div>
          <UserAccessor {...this.props}/>
          <div className="container mt-5">
            <div className="row">
              <div className="col text-center">
                <CustomMaterialButton
                  widthauto="true"
                  onClick={() => this.logout()}
                  className={"error"}
                  text={"Se dconnecter"}
                  style={{
                    backgroundColor: "#F44336",
                    color: "white"
                  }}
                />
              </div>
            </div>
          </div>
        </Layout>
      </>
    )
  }
}
