import React from 'react';

export default function ProjectMembers(props) {
  return (
    <>
      {props.project &&
      <>
        <div className={"d-flex"}>
          <div className="col-6">
            <p>{props.project.initiator.firstName} (vous)</p>
          </div>
          <div className="col-6">
            <p>{props.project.initiator.email}</p>
          </div>
        </div>
        {props.project.members.filter(member => member['@id'] !== props.project.initiator['@id']).map((member, index) => (
          <div key={index} className={"d-flex"}>
            <div className="col-6">
              <p>{member.firstName}</p>
            </div>
            <div className="col-6">
              <p>{member.email}</p>
            </div>
          </div>
        ))}
      </>
      }
    </>
  );
}
