import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link, Redirect } from 'react-router-dom';
import PropTypes from 'prop-types';
import { retrieve, reset } from '../../actions/project/show';
import { del } from '../../actions/project/delete';
import { update } from '../../actions/project/update';
import { update as updateMessage  } from '../../actions/message/update';
import redirectToLoginIfNotConnected from "../../utils/redirectToLoginIfNotConnected";
import {update as updateUser} from "../../actions/user/update";
import arrayRemove from "../../utils/arrayRemove";
import projectAlreadyBoostedChecker from "../../services/projectAlreadyBoostedChecker";
import ProjectBody from "./ProjectBody";
import jsonLDFlattener from "../../utils/jsonLDFlattener";
import {CustomLoader} from "../block/CustomLoader";

class Show extends Component {
  static propTypes = {
    retrieved: PropTypes.object,
    loading: PropTypes.bool.isRequired,
    error: PropTypes.string,
    eventSource: PropTypes.instanceOf(EventSource),
    retrieve: PropTypes.func.isRequired,
    reset: PropTypes.func.isRequired,
    deleteError: PropTypes.string,
    deleteLoading: PropTypes.bool.isRequired,
    deleted: PropTypes.object,
    del: PropTypes.func.isRequired,
  };
  constructor(props){
    super(props);
    this.state = {
      projectLiking: false,
      messageLiking: false
    }
  }

  componentDidMount() {
    this.props.retrieve(decodeURIComponent(this.props.match.params.id));
  }

  componentWillUnmount() {
    this.props.reset(this.props.eventSource);
  }

  del = () => {
    if (window.confirm('Are you sure you want to delete this item?'))
      this.props.del(this.props.retrieved);
  };

  removeFromPotentialLikes = (user, message) => {
    if (user.likedMessages.includes(message['@id'])) {
        this.props.updateUser(user, {likedMessages: user.likedMessages.filter(likedMessage => (likedMessage !== message['@id']))})
    }
  };

  removeFromPotentialDislikes = (user, message) => {
    if (user.dislikedMessages.includes(message['@id'])) {
        this.props.updateUser(user, {dislikedMessages: user.dislikedMessages.filter(dislikedMessage => (dislikedMessage !== message['@id']))})
    }
  };

  handleMessageLike = message => {
    const user = this.props.authenticated ? (this.props.userUpdated ? this.props.userUpdated : this.props.userRetrieved) : false;

    if (!user) {
      redirectToLoginIfNotConnected(this.props);
    } else {
      this.removeFromPotentialDislikes(user, message);
      this.setState({messageLiking: message['@id']});

      if (!user.likedMessages.includes(message['@id'])) {
        this.props.updateMessage(message, {likes: (message['likes'] + (user.dislikedMessages.includes(message['@id']) ? 2 : 1))})
          .then(() => {
            this.props.updateUser(user, {likedMessages: [...user.likedMessages, message['@id']]})
              .then(() => {
                this.props.retrieve(decodeURIComponent(this.props.match.params.id))
                  .then(() => {
                    this.setState({messageLiking: false});
                  });
              })
          })
      } else {
        this.props.updateMessage(message, {likes: message['likes'] - 1})
          .then(() => {

            this.props.updateUser(user, {likedMessages: user.likedMessages.filter(likedMessage => (likedMessage !== message['@id']))})
              .then(() => {
                this.props.retrieve(decodeURIComponent(this.props.match.params.id))
                  .then(() => {
                      this.setState({messageLiking: false});
                    });
              })
          })
      }

    }

  };

  handleMessageDislike = message => {
    const user = this.props.authenticated ? (this.props.userUpdated ? this.props.userUpdated : this.props.userRetrieved) : false;

    if (!user) {
      redirectToLoginIfNotConnected(this.props);
    } else {
      this.removeFromPotentialLikes(user, message);
      this.setState({messageLiking: message['@id']});

      if (!user.dislikedMessages.includes(message['@id'])) {
        this.props.updateMessage(message, {likes: (message['likes'] - (user.likedMessages.includes(message['@id']) ? 2 : 1))})
          .then(() => {
            this.props.updateUser(user, {dislikedMessages: [...user.dislikedMessages, message['@id']]})
              .then(() => {
                this.props.retrieve(decodeURIComponent(this.props.match.params.id))
                  .then(() => {
                    this.setState({messageLiking: false});
                  });
              })
          })
      } else {
        this.props.updateMessage(message, {likes: message['likes'] + 1})
          .then(() => {
            this.props.updateUser(user, {dislikedMessages: user.dislikedMessages.filter(dislikedMessage => (dislikedMessage !== message['@id']))})
              .then(() => {
                this.props.retrieve(decodeURIComponent(this.props.match.params.id))
                  .then(() => {
                    this.setState({messageLiking: false});
                  });
              })
          })
      }

    }
  };

  handleProjectLike = (item) => {
    const user = this.props.authenticated ? (this.props.userUpdated ? this.props.userUpdated : this.props.userRetrieved) : false;

    if (!user) {
      redirectToLoginIfNotConnected(this.props);
    } else {
      if (!projectAlreadyBoostedChecker(item['@id'], user.supportedProjects)) {
        let supportedProjects = jsonLDFlattener(user.supportedProjects);
        supportedProjects.push(item['@id']);
        this.setState({projectLiking: true});
        this.props.update(item, {likes: item['likes'] + 1})
          .then(() => {
            this.props.updateUser(user, {supportedProjects: supportedProjects})
              .then(() => {
                this.setState({projectLiking: false});
              })
          })
      } else {
        this.setState({projectLiking: true});
        this.props.update(item, {likes: item['likes'] - 1})
          .then(() => {
            this.props.updateUser(user, {supportedProjects: arrayRemove(jsonLDFlattener(user.supportedProjects), item['@id'])})
              .then(() => {
                this.setState({projectLiking: false});
              })
          })
      }
    }
  };
  projectMemberChecker = (item) => {
    const user = this.props.authenticated ? (this.props.userUpdated ? this.props.userUpdated : this.props.userRetrieved) : false;

    let check = false;
    if (!user) {
      return check;
    }
    item.members.forEach(member => {
      if (member['@id'] === user['@id']) {
        check = true
      }
    });
    return check;
  };

  render() {
    if (this.props.deleted) return <Redirect to=".." />;

    const user = this.props.authenticated ? (this.props.userUpdated ? this.props.userUpdated : this.props.userRetrieved) : false;

    return (
        <div className="container">
          <div className="row">
            <div className="col">
              {this.props.loading &&
                <div className="mt-3">
                  <CustomLoader/>
                </div>
              }
              {this.props.error && (
                <div className="alert alert-danger" role="alert">
                  <span className="fa fa-exclamation-triangle" aria-hidden="true" />{' '}
                  {this.props.error}
                </div>
              )}
              {this.props.deleteError && (
                <div className="alert alert-danger" role="alert">
                  <span className="fa fa-exclamation-triangle" aria-hidden="true" />{' '}
                  {this.props.deleteError}
                </div>
              )}
              {this.props.retrieved && (
                  <ProjectBody
                    project={this.props.retrieved}
                    handleProjectLike={this.handleProjectLike}
                    handleMessageLike={this.handleMessageLike}
                    handleMessageDislike={this.handleMessageDislike}
                    messageLiking={this.state.messageLiking}
                    projectLiking={this.state.projectLiking}
                    user={user}
                  />
              )}
            </div>
          </div>
        </div>
    );
  }

  renderLinks = (type, items) => {
    if (Array.isArray(items)) {
      return items.map((item, i) => (
        <div key={i}>{this.renderLinks(type, item)}</div>
      ));
    }

    return (
      <Link to={`../${type}/show/${encodeURIComponent(items)}`}>{items}</Link>
    );
  };
}

const mapStateToProps = state => ({
  retrieved: state.project.show.retrieved,
  error: state.project.show.error,
  loading: state.project.show.loading,
  eventSource: state.project.show.eventSource,
  deleteError: state.project.del.error,
  deleteLoading: state.project.del.loading,
  deleted: state.project.del.deleted,
  updated: state.project.update.updated,
  authenticated: state.authentication.authenticated,
  userUpdated: state.user.update.updated,
  userRetrieved: state.user.show.retrieved
});

const mapDispatchToProps = dispatch => ({
  retrieve: id => dispatch(retrieve(id)),
  del: item => dispatch(del(item)),
  reset: eventSource => dispatch(reset(eventSource)),
  update: (item, values) => dispatch(update(item, values)),
  updateMessage: (item, values) => dispatch(updateMessage(item, values)),
  updateUser: (item, values) => dispatch(updateUser(item, values))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Show);
