import React from 'react'
import { Field, reduxForm } from 'redux-form'
import {FormControlLabel, Typography} from "@material-ui/core";
import Checkbox from "@material-ui/core/Checkbox";
import CustomMaterialButton from "../../utils/CustomMaterialButton";

const renderCheckbox = ({ input, label }) => (
  <div>
    <FormControlLabel
      control={
        <Checkbox
          checked={input.value ? true : false}
          onChange={input.onChange}
        />
      }
      label={label}
    />
  </div>
);

const categories = [
  'Solidarit',
  'cologie',
  'Mode & design',
  'Artisanat',
  'Alimentation',
  'Agriculture',
  'Musique',
  'Sport',
  'Technologie',
  'Art & photo',
  'Patrimoine',
  'Livres',
  'BD',
  'Sant & ducation',
  'Films & vido',
  'Jeux',
  'Journalisme',
  'Thtre & danse'
];


const WizardProjectFormThirdPage = (props) => {
  const { handleSubmit, pristine, previousPage, submitting } = props;

  return (
    <form onSubmit={handleSubmit}>
      <div>
        <Typography variant={"h5"}>Choisissez les catgories associes  votre projet</Typography>
      </div>
      <div className="d-flex flex-wrap mt-3">
        {categories.map((categorie, index) => (
          <div key={index}>
            <Field name={'category-' + categorie} component={renderCheckbox} label={categorie} />
          </div>
        ))}
      </div>
      <div className="d-flex justify-content-center">
        <div className="mt-3" onClick={previousPage}>
          <CustomMaterialButton color={'secondary'} text={'Prcdent'}/>
        </div>
        <div className="mx-3">
        </div>
        <div className="mt-3 text-center">
          <CustomMaterialButton disabled={pristine || submitting} type="submit" color={'primary'} text={'Initier'}/>
        </div>
      </div>
    </form>
  )
}
export default reduxForm({
  form: 'project-wizard', //Form name is same
  destroyOnUnmount: false,     // <------ preserve form data
  enableReinitialize: true,
  keepDirtyOnReinitialize: true
})(WizardProjectFormThirdPage)
