import React from 'react';
import {Create} from "../message";
import Message from "../message/Message";

const Forum = (props) => (
    <div>
            <div className={"mt-3"}>
              <Create project={props.project} forum={props.project.forum}/>
            </div>
            {props.project.forum.messages.length > 0 ? (
              <div className="mt-3">
              {props.project.forum.messages.sort( (a, b) => b.likes - a.likes).map((message, index) => (
                  <div key={index} className={"mb-3"}>
                    <Message
                      message={message}
                      handleMessageLike={props.handleMessageLike}
                      handleMessageDislike={props.handleMessageDislike}
                      messageLiking={props.messageLiking}
                      user={props.user}
                    />
                  </div>
                ))
              }
              </div>
            ) : (
              'Il n\'y a pas encore de message dans cette conversation'
            )}
    </div>
);
export default Forum
