import React from 'react'
import { reduxForm } from 'redux-form'
import {Typography} from "@material-ui/core";
import CustomMaterialButton from "../../utils/CustomMaterialButton";
import EditorField from "../../utils/EditorField";

const WizardProjectFormSecondPage = (props) => {
  const { handleSubmit, previousPage } = props;

  return (
    <form onSubmit={handleSubmit}>
      <div>
        <Typography variant={"h5"}>Dcrivez votre projet</Typography>
      </div>
      <div className="mt-3">
        <EditorField
          name="description"
          id="inputEditorText"
          disabled={false}
          placeholder="crivez ici"
        />
      </div>
      <div className="d-flex justify-content-center">
        <div className="mt-3" onClick={previousPage}>
          <CustomMaterialButton color={'secondary'} text={'Prcdent'}/>
        </div>
        <div className="mx-3">
        </div>
        <div>
          <div className="mt-3">
            <CustomMaterialButton type="submit" className="next" color={'primary'} text={'Suivant'}/>
          </div>
        </div>
      </div>
    </form>
  )
}

export default reduxForm({
  form: 'project-wizard',  //Form name is same
  destroyOnUnmount: false,     // <------ preserve form data
  enableReinitialize: true,
  keepDirtyOnReinitialize: true
})(WizardProjectFormSecondPage)
