import React from "react";
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import {CustomLoader} from "../block/CustomLoader";
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(3, 2),
  },
}));

export default function Message(props) {
  const classes = useStyles();

  return (
    <Paper className={classes.root}>
      <div className="d-flex flex-column">
        <div className="d-flex justify-content-between">
          <div className="d-flex flex-column">
            <Typography variant="h6" component="h3">
              {props.message.author.firstName}
            </Typography>
            {props.message.createdAt &&
            <Typography variant="caption" display="block" gutterBottom>
              {props.message.createdAt.split(' ')[1]}
              <br/>
              {props.message.createdAt.split(' ')[0]}
            </Typography>
            }
          </div>
            {props.messageLiking && props.message['@id'] === props.messageLiking &&
              <div className={"mr-5 mt-2"}>
                <CustomLoader size={30}/>
              </div>
            }
            {(!props.messageLiking || (props.messageLiking && (props.messageLiking !== props.message['@id']))) &&
            <div className={"d-flex flex-md-column justify-content-center text-center align-items-center"}>
              <IconButton aria-label="up"
                          size={"small"}
                          onClick={() => props.handleMessageLike(props.message)}
                          color={
                            props.user
                              ? props.user.likedMessages.includes(props.message['@id']) ? 'primary' : 'secondary'
                              : 'secondary'
                          }>
                <ArrowUpwardIcon fontSize={"large"} />
              </IconButton>
              <span className={"mx-3"}>{ props.message.likes }</span>
              <IconButton aria-label="down"
                          size={"small"}
                          onClick={() => props.handleMessageDislike(props.message)}
                          color={
                            props.user
                              ? props.user.dislikedMessages.includes(props.message['@id']) ? 'primary' : 'secondary'
                              : 'secondary'
                          }>
                <ArrowDownwardIcon fontSize={"large"} />
              </IconButton>
            </div>
            }

        </div>

        <div className="">
          <Typography component="p">
            {props.message.content}
          </Typography>
        </div>
      </div>
    </Paper>
  )
}
