import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { create, reset } from '../../actions/project/create';
import Layout from "../../components/block/Layout";
import WizardProjectForm from "./WizardProjectForm";
import {CustomLoader} from "../block/CustomLoader";

class Initiate extends Component {
    static propTypes = {
        error: PropTypes.string,
        loading: PropTypes.bool.isRequired,
        created: PropTypes.object,
        eventSource: PropTypes.instanceOf(EventSource),
        create: PropTypes.func.isRequired,
        reset: PropTypes.func.isRequired
    };

    componentWillUnmount() {
      this.props.reset(this.props.eventSource);
    }

    handleSubmit = values => {
        console.log(values)
          this.props.create(values)
            .then(() => {
              this.props.history.push(`les-projets/${encodeURIComponent(this.props.created['@id'])}`);
            })
            .catch((e) => {
              console.log(e)
            })
    }

    render() {
      return (
        <Layout {...this.props}>
          <div className="form-center">
            <div className="container">
              <div className="row">
                <div className="col">
                  {this.props.loading && (
                    <CustomLoader/>
                  )}
                  {this.props.error && (
                    <div className="alert alert-danger" role="alert">
                      <span className="fa fa-exclamation-triangle" aria-hidden="true" />{' '}
                      {this.props.error}
                    </div>
                  )}

                  <WizardProjectForm onSubmit={this.handleSubmit} values={this.props.item} />
                </div>
              </div>
            </div>
          </div>
        </Layout>
      );
    }
}

const mapStateToProps = state => {
  const { created, error, loading } = state.project.create;
  return { created, error, loading };
};

const mapDispatchToProps = dispatch => ({
  create: values => dispatch(create(values)),
  reset: eventSource => dispatch(reset(eventSource))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Initiate);
